import $ from "jquery"
import store from './main.js'

$.ajaxSetup({
  xhrFields: {
    withCredentials: true
  },
  crossDomain: true
});

global.apiHost = client.getServerHost();
// global.apiHost = "http://192.168.1.155:88/";
// global.apiHost = "http://preroot.dotwintech.com:3088/";

global.getPic = function (img) {
  console.log(img);
  store.commit('updateImg', {
    imgName: img,
    showUpload: true
  });
  store.commit('updateUploadingStatus', {
    uploading: false
  })
};

//
global.qrCallBack = function (info) {
  // checkuid(info);
  store.commit('updateRouterId', {routerId: info});
};

/**
 * 
 * info: 
 */
global.checkuid = function (info) {
  if (!info) {
    return;
  }

  // store.commit('updateLoadingStatus', {isLoading: true});
  $.ajax({
    type: 'get',
    url: 'http://proxy.dotwintech.com:9000/manage/cgi/api!getRelayInfoByUid.action',
    xhrFields: {
      withCredentials: false
    },
    async: false,
    data: {
      uid: info
    },
    context: this,
  }).done(res => {
    // store.commit('updateLoadingStatus', {isLoading: false});
    if (res == 0) {
      store.commit('updateOnlineStatus', {isOnline: false});
      // store.commit('updateAlertStatus', {
      //   isAlert: true,
      //   aTitle: '',
      //   aContent: ',',
      // })
    } else {
      store.commit('updateOnlineStatus', {isOnline: true});
    }
  }).fail(res => {
    // store.commit('updateLoadingStatus', {isLoading: false});
    store.commit('updateAlertStatus', {
      isAlert: true,
      aTitle: '',
      aContent: '',
    })
  });
};

global.saveUserInfo = function (account, password) {
  client.setUserName(account);
  client.setPassword(password);
};

global.getUserInfo = function () {
  const account = client.getUserName();
  const password = client.getPassword();
  if (account && password) {
    autoLogin(account, password);
  } else {
    store.commit('updateLoginStatus', {loginStatus: false})
  }
};

//
global.autoLogin = function (account, password) {
  $.ajax({
    url: apiHost + 'router_stat/auth/api/login',
    dataType: 'json',
    type: 'get',
    async: false,
    context: this,
    data: {
      name: account,
      password: password
    }
  }).done(res => {
    if (res.result === 1) {
      saveUserInfo(account, password);
      store.commit('updateLoginStatus', {loginStatus: true})
    } else if (res.result === 0) {
      store.commit('updateLoginStatus', {loginStatus: false});
      store.commit('updateAlertStatus', {
        isAlert: true,
        aTitle: '',
        aContent: '',
      })
    }
  }).fail(res => {
    store.commit('updateLoginStatus', {loginStatus: false});
    store.commit('updateAlertStatus', {
      isAlert: true,
      aTitle: '',
      aContent: "",
    })
  })
};

