
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},

components: {
XInput,
XButton,
Group,
Loading,
Alert,
Flexbox,
FlexboxItem,
XDialog,
},

data () {
return {
account: '',
password: '',
isDisabled: true,
showLoading: false,
loadingText: '...',
showAlert: false,
alertTitle: '',
alertContent: '',
showLoginbox: false
}
},

created(){
},

methods: {
checkValue(value){
if (this.account && this.password) {
this.isDisabled = false;
} else {
this.isDisabled = true;
}
},

visitLogin(){
this.account = 'visitor';
this.password = '123456';
this.login();
},

accountLogin(){
this.showLoginbox = true;
},

login(){
this.showLoginbox = false;
this.$http.ajax({
url: apiHost + 'router_stat/auth/api/login',
dataType: 'json',
type: 'get',
context: this,
data: {
name: this.account,
password: this.password
},
beforeSend(){
this.showLoading = true;
}
}).done(res => {
this.showLoading = false;
if (res.result === 1) {
saveUserInfo(this.account, this.password);
this.$router.replace({name: 'home'});
} else if (res.result === 0) {
this.alertTitle = "";
this.alertContent = "";
this.showAlert = true;
}
}).fail(res => {
this.alertTitle = "";
this.alertContent = res.responseText;
this.showAlert = true;
})
}
}
}



