
import Alert from 'vux/src/components/alert/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


import EXIF from 'exif-js' //

export default {
directives: {
TransferDom
},

components: {
Alert,
Loading,
Toast,
XHeader,
XInput,
Cell,
CellBox,
XButton,
Group,
Grid,
GridItem,
PopupPicker,
Flexbox,
FlexboxItem,
Previewer
},

data() {
return {
apiHost: '',
isHandle: true,
isShowImg: false,
pickerVal: [],
pickerList: [],
showSaveState: false,
toastType: 'text',
saveText: '',
alertTitle: '',
alertContent: '',
buttonText: '',
showAlert: false,
savedData: {},
reportObj: {
wifiList: [],
lbsList: [],
addr: '',
userName: '',
userPhone: ''
},
showImg: false,
imgData: '',
imgUrl: '',
showlbs: false,
showwifi: false,
uploading: false,
alert1: false,
showLoading: false,
loadingText: ''
}
},

created(){
this.getGpsData();
},

computed: {









getRouteId(){
return this.$store.state.routerId;
}
},

methods: {







/**
* 
* qrCallBack: 
*/
scanCode(){
client.qr("qrCallBack");
},

/**
* Id
*/
updateRouteId(val){
this.$store.commit('updateRouterId', {routerId: val})
},

checkUid(val){
checkuid(val)
},












/**
* 
*/
getImg(e){
const file = e.target.files[0],
reader = new FileReader(),
maxSize = 200 * 1024;
reader.onload = function () {
const result = reader.result;
this.showImg = true;
this.imgUrl = result;
if (result.length > maxSize) {
this.compress(result);
} else {
this.imgData = result.replace(/^data:image\/(jpeg|jpg|png);base64,/, "");
}
}.bind(this);
reader.readAsDataURL(file);
},

/**
* 
* @img
*/
compress(result){
const img = new Image();
img.src = result;
img.onload = function () {
const canvas = document.createElement('canvas'),
ctx = canvas.getContext('2d');
canvas.width = img.width;
canvas.height = img.height;
ctx.drawImage(img, 0, 0);

const data = canvas.toDataURL('image/jpeg', 0.2);
this.imgData = data.replace(/^data:image\/(jpeg|jpg|png);base64,/, "");
}.bind(this);
},

/**
* Blob
*/


showImgDetail(){
this.isShowImg = true;
},


deleteImg(){




this.imgUrl = '';
this.imgData = '';
this.showImg = false;
this.isShowImg = false;
},

/**
*
* formData:
*/
checkData(formData){
if (this.checkSubmit(formData, this.savedData)) {
this.showSaveState = true;
this.saveText = '';
this.toastType = 'warn';
return false;
} else if (!formData.addr) {
this.showSaveState = true;
this.saveText = '';
this.toastType = 'warn';
return false;
} else if (!formData.routerId) {
this.showSaveState = true;
this.saveText = 'Id';
this.toastType = 'warn';
return false;
} else if (!formData.userName) {
this.showSaveState = true;
this.saveText = '';
this.toastType = 'warn';
} else if (!formData.userPhone) {
this.showSaveState = true;
this.saveText = '';
this.toastType = 'warn';
}














else {
return true;
}
},

/**
* 
* objA:
* objB:
*/
checkSubmit(objA, objB){
const aProps = Object.getOwnPropertyNames(objA);
const bProps = Object.getOwnPropertyNames(objB);

if (aProps.length !== bProps.length) {
return false;
}

for (let i = 0; i < aProps.length; i++) {
let propName = aProps[i];
if (objA[propName] !== objB[propName]) {
return false;
}
}
return true;
},

/**
* 
*/
saveData(){
this.reportObj.routerId = this.$store.state.routerId;

this.reportObj.city_name = this.getCityName(this.reportObj.addr);

if (!this.checkData(this.reportObj)) {
return;
}
console.log(this.imgData);

this.$http.ajax({
url: apiHost + "router_stat/api/router/report",
dataType: 'json',
type: 'post',
data: {
data: JSON.stringify(this.reportObj),
img: this.imgData
},
context: this,
beforeSend(){
this.showLoading = true;
this.loadingText = '...'
},
complete(){
this.showLoading = false;
}
}).done(res => {
if (res.result === 1) {






this.showSaveState = true;
this.saveText = '';
this.toastType = 'success';
Object.assign(this.savedData, this.reportObj);
this.$store.commit('updateRouterId', {routerId: ''});
this.showImg = false;
this.imgData = '';
this.imgUrl = '';




} else {
this.showSaveState = true;
this.saveText = '';
this.toastType = 'warn';
}
}).fail(res => {
this.$store.commit('updateAlertStatus', {
isAlert: true,
aTitle: '',
aContent: res.statusText,
})
})
},

getCityName(value){
let startIndex = value.indexOf("");
let endIndex = value.indexOf("");
return value.substring(startIndex + 1, endIndex + 1);
},

/**
* 
*/
getGpsData(){
let resp;
let times = 0;
resp = client.getGps();
if (resp === 'gpsNotOpen') {
this.alert1 = true;
return;
}
this.$store.commit('updateLoadingStatus', {
isLoading: true,
loadingText: '...'
});
let t = setInterval(() => {
resp = client.getGps();
if (times === 30) {
clearInterval(t);
this.$store.commit('updateLoadingStatus', {
isLoading: false,
loadingText: ''
});
this.showAlert = true;
this.alertTitle = '';
this.alertContent = '';
this.buttonText = '';
} else if (resp !== "timeout") {
clearInterval(t);
console.log(resp);
this.$store.commit('updateLoadingStatus', {
isLoading: false,
loadingText: ''
});
Object.assign(this.reportObj, JSON.parse(resp));
}
times++;
console.log(times);
}, 1000);
},

alertHideCallBack() {
this.showAlert = false;
this.getGpsData();
},

alertCallBack(){
this.$router.replace({name: 'home'});
}
},

filters: {
getArea(value){
const index = value.indexOf("");
return value.substring(0, index + 1);
}
}
}



