
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default{
directives: {
TransferDom
},

components: {
XHeader,
},
data(){
return {
newList: [],
pageSize: 10,
pageNo: 1,
totalRows: null,
installList: [],
allLoaded: false,
isShowImg: false,
imgUrl: ''
}
},

created(){
this.getList();
this.installList = this.newList;
if(this.installList.length === this.totalRows){
this.allLoaded = true;
}
},
methods: {
/**
* 
*/
getList(){
this.$http.ajax({
url: apiHost + "router_stat/api/router/list",
dataType: 'json',
type: 'POST',
async: false,
data: {
nextpage: JSON.stringify({
pageNo: this.pageNo,
pageSize: this.pageSize,
sortname: "log_time",
sortorder: "DESC"
})
},
context: this,
}).done(res => {
if (res.result === 1) {
this.totalRows = res.page.totalRows;
this.pageNo++;
this.newList = res.list;
}
}).fail(res => {
this.$store.commit('updateAlertStatus', {
isAlert: true,
aTitle: '',
aContent: res.responseText,
})
});
},
/**
* @param item
* @returns {string}
*/
getImgUrl(imgName){
return apiHost + "router_upload/" + imgName;
},
/**
* @param timeStamp
* @returns {string}
*/
timeTransform(timeStamp){
return new Date(parseInt(timeStamp) * 1000).toLocaleString();
},
/**
* 
*/
loadBottom(){
this.getList();
setTimeout(() => {
this.installList.push(...this.newList);
console.log(this.installList.length);
console.log(this.totalRows);
if (this.installList.length === this.totalRows) {
this.allLoaded = true;
}
this.$refs.loadmore.onBottomLoaded();
}, 2500);
},

/**
* 
*/
showImgDetail(img){
this.imgUrl = apiHost + "router_upload/" + img;
this.isShowImg = true;
},
}
}



