// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import Vuex from 'vuex'
import Mint from 'mint-ui'
import 'mint-ui/lib/style.css'
import $ from "jquery"
import App from './App'
import Login from './components/Login'
import Home from './components/Home'
import Setting from './components/Setting'
import Report from './components/Report.vue'
import InstallList from './components/InstallList.vue'
import './config'
import './assets/font-awesome-4.7.0/css/font-awesome.min.css'

Vue.use(VueRouter);
Vue.use(Vuex);
Vue.use(Mint);

$.ajaxSetup({
  xhrFields: {
    withCredentials: true
  },
  crossDomain: true
});

Vue.prototype.$http = $;

const store = new Vuex.Store({
  state: {
    loginStatus: false,
    imgName: '',
    showUpload: false,
    routerId: '',
    isLoading: false,
    loadingText:'',
    isAlert: false,
    aTitle:'',
    aContent:'',
    isOnline: false,
    uploading: false,
  },
  mutations: {
    updateLoginStatus(state, payload){
      state.loginStatus = payload.loginStatus;
    },
    updateRouterId (state, payload) {
      state.routerId = $.trim(payload.routerId).toLowerCase()
    },
    updateImg(state, payload){
      state.showUpload = payload.showUpload;
      state.imgName = payload.imgName
    },
    updateLoadingStatus (state, payload) {
      state.isLoading = payload.isLoading;
      state.loadingText = payload.loadingText;
    },
    updateAlertStatus (state, payload) {
      state.isAlert = payload.isAlert;
      state.aTitle = payload.aTitle;
      state.aContent = payload.aContent;
    },
    updateOnlineStatus (state, payload) {
      state.isOnline = payload.isOnline;
    },
    updateUploadingStatus (state, payload) {
      state.uploading = payload.uploading;
    },
  }
});

/**
 * 
 */
const routes = [
  {
    path: '/',
    name: 'login',
    component: Login,
    beforeEnter: (to, from, next) => {
      getUserInfo();
      if(store.state.loginStatus){
        next('/home')
      }else{
        next()
      }
    }
  },
  {
    path: '/home',
    name: 'home',
    component: Home
  },
  {
    path: '/setting',
    component: Setting
  },
  {
    path: '/report',
    component: Report
  },
  {
    path: '/installList',
    component: InstallList
  }
];

const router = new VueRouter({routes});

router.beforeEach(function (to, from, next) {
  store.commit('updateLoadingStatus', {isLoading: true});
  next();
});

router.afterEach(() => {
  store.commit('updateLoadingStatus', {isLoading: false})
});

FastClick.attach(document.body);

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  store,
  router,
  render: h => h(App),
  mounted(){
    (function (doc, win) {
      let docEl = doc.documentElement,
        resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize',
        recalc = function () {
          let clientWidth = docEl.clientWidth;
          if (!clientWidth) return;
          docEl.style.fontSize = 100 * (clientWidth / 320) + 'px';
        };
      if (!doc.addEventListener) return;
      win.addEventListener(resizeEvt, recalc, false);
      doc.addEventListener('DOMContentLoaded', recalc, false);
    })(document, window);
  }
}).$mount('#app-box');

export default store;



